import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      redirect: '/home',
      component: () => import('@/layout/index.vue'),
      children: [
        {
          path: '/home',
          name: 'ArticlesList',
          component: () =>
            import('@/views/articles/list')
        },
        {
          path: '/article/detail/:id',
          name: 'Detail',
          component: () =>
            import('@/views/articles/detail')
        },
        {
          path: '/article/keywords',
          name: 'SearchResult',
          component: () =>
            import('@/views/articles/searchResult')
        },
        {
          path: '/about',
          name: 'AboutMe',
          component: () =>
            import('@/views/about/about')
        },
        {
          path: '/tags',
          name: 'Tags',
          component: () =>
            import('@/views/tags/tags')
        },
        {
          path: '/tags/:id',
          name: 'TagsItem',
          component: () =>
            import('@/views/tags/tagsItem')
        },
        {
          path: '/categories',
          name: 'Categories',
          component: () =>
            import('@/views/categories/categories')
        },
        {
          path: '/categories/:id',
          name: 'CategoryItem',
          component: () =>
            import('@/views/categories/categoryItem')
        },
        {
          path: '/archives',
          name: 'Archives',
          component: () =>
            import('@/views/archives/archives')
        },
        {
          path: '/archive/:timeline',
          name: 'ArchiveTimeline',
          component: () =>
            import('@/views/archives/archiveTimeRange')
        }
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('@/views/user/login.vue')
    },
    {
      path: '/signup',
      name: 'signup',
      component: () => import('@/views/user/signup.vue')
    },
    {
      path: '/activation',
      name: 'activation',
      component: () => import('@/views/user/activation.vue')
    }
  ]
})

export default router



